.project-scroll-main{
          width:100%;
          height:100%;
          display: flex;
          flex-direction: column;
          position: relative;
}
.project-scroll-main div.img p.title{
          position: absolute;
          bottom:0px;
          background-color: grey;
          color:green;
          font-size: 30px;
          font-weight: 600;
          bottom: 0;
          left: 0;
          border-radius: 5px;
          border: 1px solid green;

}
.project-scroll-main div{
          width:100%;
          height:80%;
          position: relative;
          
          
}
.project-scroll-main div img{
          width:100%;
          height:100%;
}
.project-scroll-main span{
          width:100%;
          height: 20%;
          background-color: black;
          padding:0px 10px;
          display: flex;
          align-items: center;
          color: rgb(36, 165, 36);
          font-size: 10px;
}