.App {
  text-align: center;
  background-image: url('./COMPONENTS/CSSS/background_walpaper.jpg');
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

i:hover {
  color: rgb(38, 145, 3);
  /* ?? */

}



a {
  text-decoration: none;
  color: black;

}




.App .nav .head .dd a:hover {
  color: rgb(11, 116, 11);
  transform: scale(1.2);
}
.App .nav .head .dd p:hover{
  color: rgb(11, 116, 11);
  transform: scale(1.2);
  cursor: pointer;
  
}
.active{
  
  color: rgb(6, 168, 6);
  font-weight: 700;
  
  
}

.nav {
  width: 100%;
  position: fixed;

  display: flex;
  justify-content: center;
  z-index: 4;

}

.head {
  width: 40%;
  
  display: flex;
  justify-content: space-between;
  align-Items: center;
  flex-Wrap: wrap;
  transition: 1s;
  padding: 0px 50px;
  color: black;
  border-radius: 15px;
  
  font-size: 20px;
  position: relative;
  
  



  
}

.head span{
  position: relative;
}
.head span#drop-span .dropdown-contact{
  position:absolute;
  top:100%;
  right:0%;
  border:1px solid green;
  width:500px;
  height:auto;
  border-radius: 10px;
  overflow: hidden;
  z-index: 8;
  
 


}
.head span#drop-span .dropdown-contact.none{
  display:none;
}
.head span#drop-span .dropdown-contact .dropdown-content{
  display: flex;
  flex-direction: column;
  padding:20px;
  background-color: rgb(161, 156, 156);

}
.head span#drop-span .dropdown-contact .dropdown-content span{
  display:flex;
  flex-direction: column;
  font-size: 20px;
  margin:10px 0px;
  align-items: flex-start;

}
.head span#drop-span .dropdown-contact .dropdown-content span p{
  font-size: 20px;
}
.head span#drop-span .dropdown-contact .dropdown-content span p:hover{
  color: black;
  transform: none;

}
.head span#drop-span .dropdown-contact .dropdown-content span input{
  width:80%;
  margin:auto;
  padding:10px;
  border-radius: 5px;
  border: none;
  outline:none;
  font-size: 15px;
  height: fit-content;
}
.head span#drop-span .dropdown-contact .dropdown-content span textarea{
  width:80%;
  margin:auto;
  padding:10px;
  border-radius: 5px;
  border: none;
  outline:none;
  font-size: 15px;
  

}
.head span#drop-span .dropdown-contact .dropdown-content span input::placeholder{
  font-size: 15px;
  color: grey;
}

.head span#drop-span .dropdown-contact .dropdown-content span.btn button{
  background-color: green;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
  width: fit-content;
  margin: auto;
  outline: none;
}
.head span#drop-span .dropdown-contact .dropdown-content span.btn button:hover{
  background-color: rgb(13, 78, 13);
}


.dd p {
  
  display: inline;
  color: black;
}
p.black{
  color: white;

}

.head.black {
  background-color: rgb(7, 7, 7);
  box-shadow: 0px 2px 4px 0px gray;
  color:white


}
.black{
  color: white;
}



.App .nav .head .dd {
  font-weight: 600;
 
  position: relative;
  width:15%;
  
  padding:5px 0px;

}





.App .nav .head p {
  font-size: 20px;
}



.menubutton button{
  font-size: 20px;
  
  
  padding: 10px;
  display:none;
  justify-items: center;
  align-items: center;
  border-radius: 50%;
  color:black;
  background-color: rgb(70, 128, 235);
  box-shadow: 0px 2px 4px 0px black;
  border: none;
  animation: btn-animation 0.5s;
  
}
@keyframes btn-animation {
  0%{
    padding:0px;
    font-size: 0px;
    background-color: black;
  }
  100%{
    padding:10px;
    font-size: 20px;
    background-color: white;

  }
  
}


/* -------------------------sidebar ------------------------ */
.App .sidebar{
  position:fixed;
  top:0%;
  right:0%;
  background-color: black;
  animation: open 1s;
  width: 20%;
  padding:10px;
  display: none;
  flex-direction: column;
  height: 100vh;
  z-index: 12;
  animation:sidebar 0.25s;
  

}
.sidebar.open{
  display:flex;
}

@keyframes sidebar{
  0%{
    width:30px;
    height:30px;
    border-radius: 50%;
    top:10px;
    right:10px

  }
  
  100%{
    width:20%;
    top:0%;
    right:0%;
    height:100vh;
    border-radius: 0%;
  }
}

.App .sidebar span{
  margin:5px 0px;
  display:flex;
  flex-direction:row;



}
.App .sidebar span.btn{
  display: flex;
  flex-direction: row-reverse;
  font-size: 30px;
  color:grey;
}
.App .sidebar span.btn i:hover{
  color:white;
  transform: rotate(30deg);

}
.App .sidebar span a{
  text-decoration: none;
  color:grey;
  font-size: 30px;
}

.App .sidebar span a:hover{
 
  color:white;
  font-weight: 700;
  transform: scale(1);
}
.app .sidebar span p{
  color:white;
  font-weight: 700;
}

Routes{
  position: relative;
  z-index: 0;
}
.dropbtn {

  color: black;

  border: none;
}





/*   
@keyframe                          description */
@keyframes txtinfo {

  0% {
    left: 100%;
  }

  100% {
    left: 0%;
  }




}

/*                                                  animated text                                                */
.descinfo {
  padding: 30px;
  font-size: 25px;
  color: rgb(194, 21, 171);
  overflow: hidden;

}

.txt {
  position: relative;
  animation: txtinfo 3s;
  font-size: 20px;
  text-align: justify;


}
.menubutton{
  border:none;
}

.descinfo:hover {
  background-color: rgb(255, 146, 228);
  color: black;
}

