@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/*                                            sample                                           */
.sample {
  width: 100%;
  padding-bottom: 10px;
  display: flex;
  justify-content: center;
  justify-items: center;
  color: aliceblue;
  font-size: 30px;

}

/*                                            slides                                            */
.slides {
  width: 100%;


}

.slidemain {
  width: 60%;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;




}

.images {
  flex-grow: 1;
  width: 100%;
  display: flex;
  justify-content: left;

  position: relative;
  z-index: 0;
  overflow: auto;


  scroll-behavior: smooth;
  cursor: pointer;
}

.images::-webkit-scrollbar {
  -webkit-appearance: none;
}

.buttons {
  width: 100%;
  display: flex;
  align-items: center;

  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.slideimg {
  min-width: 60%;
  height: 300px;
  padding: 10px;


  overflow: hidden;


}

.slideimg img {
  width: 100%;
  height: 100%;
}

/*                                                       footer                           */

.footer {
  width: 100%;
  

  position: fixed;
  bottom: 0px;
  z-index: 10;

}
.footer.none{
  display: none;
}

.socials {
  padding: 0px 30px;
  width: 40%;
  margin: auto;
  background-color: white;
  border-radius: 15px;

  display: flex;
  justify-content: space-evenly;
  align-content: center;
  flex-wrap: wrap;
  box-shadow: 0px 2px 6px 0px black;

}

.social {
  font-size: 30px;
  color: black;

}
.footer .socials .social i:hover{
  transform: scale(1.2);

}

#btn {
  color: aliceblue;
  font-size: 50px;
}



/*----------------------------------- nav media query --------------------------------------------------*/
@media (max-width:1050px) {
  
/* hide head */
  .App .nav .head .dd a {
    display: none;
  }
  .App .nav .head .dd p{
    display:none;
  }
  .head.black{
    display:none;
  }
  .head{
    display: none;
  }
  .nav{
    justify-content: end;
    padding: 2%;
  }
  .menubutton button{
    display: flex;
    border:none;
  }

}
@media (max-width:500px){
  .socials{
    width: 100%;
    border-radius: 0%;
    
    justify-content: space-between;
    transition: 1s;
    background-color:rgb(19, 22, 39);
    box-shadow: none;
  }
  .App .sidebar span a{
    font-size: 20px;
  }
  .social{
    font-size: 25px;
    color: rgb(70, 128, 235);
  }
  .App{
    background-image:none;
    background-color:rgb(19, 22, 39);
  }
  
}