.apibox-main{
          width:100%;
          height:auto;
          display: flex;
          flex-direction: column;
          background-color: rgb(192, 186, 186);

}
.apibox-main .apiname{
          width:100%;
          
          padding:5px 10px;
          font-size: 25px;
          display:flex;
          justify-content: flex-start;


}
.apibox-main .api-description{
          width:100%;
          
          padding:10px;
          font-size: 15px;
          align-items: center;
          display:flex;
          justify-content: flex-start;

          
}
.apibox-main .api-link{
          width:90%;
          margin:auto;
          height:40px;
          padding:0px 10px;
          background-color: rgb(146, 146, 146);
          overflow-x: scroll;
          display: flex;
          align-items: center;
          color:rgb(25, 139, 25);
          font-weight: 500;
          font-size: 20px;
          white-space: nowrap;
          justify-content: flex-start;
}
.apibox-main .api-link::-webkit-scrollbar{
          display: none;
}

@media (max-width:500px) {
          .apibox-main .apiname{
                    font-size: 20px;

          }
          .apibox-main .api-description{
                    font-size: 15px;

          }
          .apibox-main .api-link{
                    font-size: 10px;
                    height:30px;

          }
          
}