.home {
  width: 100%;
  display: flex;
  flex-direction: column;
  
}
.sample-projects{
  width: 100%;
  height: 100vh;
  background-color: black;
  position: fixed;
  
  top: 0;
  left:0;
  z-index: 5;
  display: none;
  flex-direction: column;
  animation: sample 0.5s;
  transition: 0.5s ease;
  
}
.sample-projects .backbtn{
  position: absolute;
  top:0%;
  right: 0;
}
.sample-projects .backbtn i{
  font-size: 30px;
  color:white;
}
@keyframes sample {
  0%{
    left:100%;

  }
  100%{
    left:0%;
  }
  
}
.sample-projects .first-row{
  width:100%;
  height: 50%;
  display: flex;
  /* display:block? */
  flex-direction: column;
 

}
.sample-projects .first-row .title{
  width:100%;
  display: flex;
  justify-items: center;
  color:white;
  font-size: 25px;
  justify-content: center;
}
.sample-projects .first-row .examples{
  width:80%;
  margin: auto;
  height:80%;
  display:flex;
  justify-content: space-between;
}
.sample-projects .first-row .examples .slider-swipe{
  width:30%;
  height:90%;
  border-radius: 10px;
  box-shadow: 4px 4px 10px 0px rgb(57, 57, 223);
  display:flex;
  flex-direction: column;
  overflow: hidden;
  background-color: #232423;

}
.sample-projects .first-row .examples .slider-swipe img{
  width:100%;
  height:90%;


}
.sample-projects .first-row .examples .slider-swipe span{
  width:100%;
  height:10%;
  padding:10px 20px;
  display: flex;
  align-items: center;
  overflow: hidden;

}
.sample-projects .first-row .examples .slider-swipe span a{
  text-decoration: none;
  color: rgb(49, 185, 37);
  font-size: 15px;
}





.sample-projects .second-row{
  width:100%;
  height: 50%;
  display: flex;

  /* display:block? */
  flex-direction: column;
  overflow-x: hidden;
  
  flex-wrap: nowrap;

}
.sample-projects .second-row .title{
  width:100%;
  display: flex;
  justify-items: center;
  color:white;
  font-size: 25px;
  justify-content: center;
}
.sample-projects .second-row .examples{
  width:80%;
  height:80%;
  margin: auto;
  display:flex;
  justify-content: space-between;
  overflow: hidden;
}
.sample-projects .second-row .examples .slider-swipe{
  width:30%;
  height:90%;
  border-radius: 10px;
  box-shadow: 4px 4px 10px 0px rgb(57, 57, 223);
  display:flex;
  flex-direction: column;
  overflow: hidden;
  background-color: #232423;

}
.sample-projects .second-row .examples .slider-swipe img{
  width:100%;
  height:90%;


}
.sample-projects .second-row .examples .slider-swipe span{
  width:100%;
  height:10%;
  padding:10px 20px;
  display: flex;
  align-items: center;
  overflow: hidden;

}
.sample-projects .second-row .examples .slider-swipe span a{
  text-decoration: none;
  color: rgb(49, 185, 37);
  font-size: 15px;
}


.zip{
  position: absolute;
  left:11%;
  bottom:20%;
  height: 200px;
  width: 90px;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  color:white;
  background-color: black;
  border-radius: 5px;
  font-size: 50px;
  z-index: 8;
  cursor:grab;


}












.sample-projects .third-row{
  width:100%;
  min-height: 200px;
  display: flex;
  /* display:block? */
  flex-direction: column;
  overflow-x: hidden;
  
  flex-wrap: nowrap;

}
.sample-projects .third-row .title{
  width:100%;
  display: flex;
  justify-items: center;
  color:white;
  font-size: 25px;
  justify-content: center;
}
.sample-projects .third-row .examples{
  width:100%;
  display:flex;
  justify-content: space-between;
}
.sample-projects .third-row .examples .img{
  width:23%;
  height: 20vh;
  
  box-shadow: 0px 4px 8px 0px rgb(72, 72, 245);
  margin:10px 10px;

}
.sample-projects .third-row .button{
  display: flex;
  flex-direction: row-reverse;
  padding: 10px;
}
.sample-projects .third-row .button button{
  padding:5px;
  border-radius: 5px;
  color:white;
  font-size: 15px;
  background-color: rgb(73, 18, 226);
  border: none;
}
.sample-projects .third-row .button button:hover{
  transform: scale(1.2);
  background-color: #2d2af7;
}
.sample-projects.open-projects{
  display:flex;
}

.main {
  width: 100%;
  height: 100vh;
}

.main .description {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0% 0px;
}

.main .description .info {
  display: flex;
  justify-content: center;
  padding: 50px;
  min-width: 50%;
  height:100%;
  justify-items: center;
  align-items: center;
 

}

.main .description .info img {
  height: 500px;
  width: 500px;
}

.main .description .info img:hover {
  transform: scale(1.2);
  transition: 0.5s;
}

.main .description .info-logo {
  max-width: 50%;
  display: flex;
  flex-direction: column;
  padding: 10px;
  height: 100%;
  justify-content: center;
  
}

.main .description .info-logo .heading {
  width: 100%;
  display: flex;
  flex-direction: column;
  color: rgb(28, 145, 28);
  justify-items: center;
  padding: 20px;
  font-size: 50px;
}
.heading p{
  font-size: 25px;
}

.main .description .info-logo .descinfo {
  padding: 30px;
  font-size: 25px;
  color: black;
  overflow: hidden;
  width: 100%;
  display: flex;
  justify-items: center;
  justify-content: center;
  border-radius: 10px;

}

.main .description .info-logo .descinfo .txt {
  position: relative;
  animation: txtinfo 3s;
  font-size: 20px;
  text-align: justify;
  color:black;


}
.main .description .info-logo .descinfo p.txt:hover{
  background:none
}
.button-project{
  
  width: 100%;
  margin: 0px auto;
  display: flex;
  justify-content: center;
}
.button-project button{
  background:none;
  border: 2px solid black;
  color:black;
  border-radius: 5px;
  font-size: 20px;
  padding: 10px;

}
.button-project button:hover{
  background-color: black;
  color: #fcfafa;
  
  
  transition: 0.3s;
}
hr{
  color:white;
}
@keyframes txtinfo {
  0% {
    left: 50%;
  }

  100% {
    left: 0%;
  }

}

.main .description .info-logo .descinfo:hover {
  background: none;
  color: black;
}
.sample-content {
  width: 100%;
  min-height: 500px;
  background-color: black;
  position: relative;
  
  display:flex;
  flex-direction: column;
  align-items: center;
}

.sample-content .title1 {
  width: 60%;
  margin:20px auto;

  
  color: white;
  overflow-x: hidden;
  


}

.sample-content .title1 h3.h-title{
  position: relative;
  left:100%;
  color:grey;
  transition: 1s;
  font-size: 50px;

}
h3.h-title.expand{
  left:0%!important;
  transition: 0.5s;
  
} 

.sample-content .sample-cards-slider{
  width:70%;
  margin:20px auto;
  
  min-height: 100px;
  
  overflow-x: auto;
}
.sample-content .sample-cards-slider .slider-swipe{
  width: 20%;
  height:300px;
  box-shadow:4px 4px 12px 0px rgb(64, 105, 240);
  margin:10px 0px;
  border-radius: 15px;
  overflow: hidden;
}
.sample-content .sample-cards-slider img{
  width:100%;
  height:100%;
  
  
  
}

.sample-animation{
  position: relative;
  width:100%;
  
  height:700px;
  

  display:flex;
  background-color: black;
  background-size: cover;
  
  flex-direction: row;
  color: white;
  overflow: hidden;
  padding: 20px 20px;
  
  

}
.sample-animation-description{
  width: 50%;
  height:100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  text-align: justify;
  padding:20px;
  overflow-x: hidden;
  
}
.sample-animation-picture{
  width: 50%;
  height:100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding:20px;

  position:relative;
  overflow: hidden;


}
.sample-animation-picture .frame{
  width:100%;
  height:100%;
  border-radius: 10px;
  box-shadow: 0px 4px 8px 0px rgb(110, 110, 196);
  position: relative;
  overflow: hidden;
}

.sample-animation-picture #backimg{
  height: 100%;
  width: 100%;
  

  position:relative;
  z-index: 1;
  

  

}
.sample-animation-picture #word1{
  width:40%;
  height:10%;
  position: absolute;
  top:10%;
  left:70%;
  z-index: 4;
  box-shadow: none;
  


}
.sample-animation-picture #animatepc{
  position:absolute;
  top:10%;
  left:50%;
  width:50%;
  height:50%;
  z-index: 5;
  box-shadow: none;
  
}
.sample-animation-picture #animatemob{
  position:absolute;
  bottom:0%;
  left:0%;
  width:20%;
  height:50%;
  z-index: 5;
  box-shadow: none;
  display: none;
  

}
[data-aos] {
  visibility: hidden;
}
[data-aos].animated {
  visibility: visible;
}

