.sample-content .project-bar{
  width:100%;
  height: 300px;
  background-color:white;
  position: relative;
  transition: 0.5s ease;
  
  

}
.sample-content .project-bar.heightfull{
  height: 700px;
  transition: 0.5s ease;
}

.sample-content .project-bar button#closeprojectbar{
  position: absolute;
  width:2%;
  height:10%;
  
  background:none;
  color:white;
  border:none;
  left:5%;
  bottom: 10%;
 
  

  border-radius: 5px;
  z-index: 7;


}
.sample-content .project-bar button#closeprojectbar img{
  width:100%;
  height: 100%;

}
.sample-content .project-bar #closeprojectbar:hover{
  
  transform: scale(1.1);
}
.sample-content .project-bar .bar-open-button{
  width:93%;
  min-height:200px;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-color: white;
  
}
.divider{
  width:100%;
  height:300px;
  background-color: white;
  display: flex;
  align-items: center;
  padding: 0px 5%;
  font-size: 60px;
  color: black;
  font-weight: 600;

}
.divider i{
  margin:30px 0px;
  font-size: 80px;
}
.divider i:hover{
  color:black;
}


.sample-content .project-bar .bar-open-button i{
  font-size: 200px;
  color:black;
  border-radius: 5px;
  border: none;
  

  transition: 0.5s;
}
.sample-content .project-bar .bar-open-button i:hover{

  color: rgb(42, 167, 42);
  transform: scale(1.1);
}
.flex{
  display: flex;
  transition: 0.5s;
}

.none{
  display: none;
  transition: 0.5s;
}
.block{
  display: block;
  transition: 0.5s;
}



.sample-content .project-bar .projects{
  width:95%;
  height:90%;
  margin: auto;
  border-radius: 10px;
  transition: 0.5s;

  background-color: rgb(0, 0, 0);
  overflow-y: auto!important;
  padding:100px 0px!important;
  position: relative;
  z-index: 4;



  

}
.sample-content .project-bar .zipper{
  width:5%;
  height: 90%;
  position: absolute;
  left: 10%;
  top: 0%;
  border-radius: 10px;
  z-index: 5;
  transition: 0.5s;
}
.sample-content .project-bar .projects::-webkit-scrollbar{
  display: none;
}

.sample-content .sample-button{
  width:100%;
  padding:10px;
  display:flex;
  margin:20px 0px;
  justify-content: center;
  align-items: center;

}
.sample-content .project-bar .projects .project-children{
  width:10%;
  height:200px!important;
  border-radius: 10px;
  overflow: hidden;
  margin:400px 0px;
  margin-left: 80%;
  opacity: 0.4;
  box-shadow: 4px 4px 8px 0px rgb(50, 35, 182);
  transition: 0.5s;
  
}
.active-project{
  
  margin-left: 60%!important;
  width: 20%!important;
  height: 300px!important;
  transform:scale(1.5);
  box-shadow: 4px 4px 8px 0px grey;
  opacity: 1!important;
  transition: 0.5s ease;


}

.sample-content .sample-button button{
  background-color: rgb(68, 65, 65);
  border-radius: 2px;
  border: none;
  font-size: 20px;
  padding:10px;
  color:rgb(253, 252, 252);


}
.sample-content .sample-button button:hover{
  background-color: #080808;
  
  color: white;
  transition: 0.5s;
}



