.card {
          width: 100%;
          display: flex;
          flex-Direction: column;
          padding: 10px;
          background-Color: rgb(240, 240, 245);
          border-Radius: 5px;
          box-Shadow: 0px 2px 5px 0px rgb(0, 0, 0, 0.4);
}

.card .account {
          width: 100%;
          display: flex;
          flex-Direction: row;
          padding: 5px;
}

.card .account .profile {
          padding: 0px 5px;
}

.card .account .profile img {
          border-Style: solid;
          border-Color: blue;
          border-Width: 3px;
          height: 40px;
          width: 40px;
          border-Radius: 50%;
}

.card .account .info {
          flex-Grow: 1;
          display: flex;
          flex-Direction: column;
          padding: 0px 5px;
}

.card .account .info .tag {
          display: flex;
          justify-Content: start;
          

        
}
.card .account .info .tag span{
          font-size: 10px;
          padding:5px;
          background-color: grey;
          box-shadow: 0px 2px 6px 0px black;
          border-radius: 5px;
          margin: 10px 0px;

}

.card .account .info .time {
          display: flex;
          justify-Content: start;
          justify-Items: start;
          font-size: 15px;
}

.card .account .button1 {
          padding: 0px 5px;

}

.card .account .button2 {
          padding: 0px 5px;
}

.card .caption {
          width: 100%;
          padding: 10px;
          font-size: 30px;
          font-weight: 600;
          color: rgb(16, 109, 16);
          display: flex;
          justify-Content: start;
          justify-Items: start;
}
.card .body{
          width: 100%;
          font-size: 20px;
          text-align:left;
}
/* .card .image{
          width: 100%;
}
.card .image img{
          width:100%;max-Height:500px;
} */

@media (max-width:500px) {
          .card{
                    padding:10px;
          }
          .card .caption{
                    font-size: 20px;
          }
          .card .body{
                    font-size: 15px;
          }

          
}